#contactPage {
    min-height: calc(100vh- 4rem);
    width: 100vw;
    max-width: 60rem;
    margin: 1rem auto;
    display: flex;
    flex-direction: column;
    align-items: center ;
    justify-content: center;
    text-align: center;
}
.contactHeader {
    font-size: 3rem;
}

.contactDes {
    margin-top: 1rem;
    font-size: medium;
    text-wrap: wrap;
}

.contactForm {
    margin: 1rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 60vw;
    max-width: 60rem;
}

.name, .email, .msg {
    font-size: medium;
    width: 100%;
    max-width: 40rem;
    margin: 0.5rem;
    padding: 0.5rem 1rem;
    color: white;
    border-radius: 0.5rem;
    border: none;
    background:rgba(15, 5, 5, 0.5);
    /* background: rgb(70,70,70); */
}

.msg {
    resize: none;
}

.submitBtn {
    background: #2C7865;
    color:white;
    border: none;
    border-radius: 1rem;
    margin-top: 1rem;
    width: 8rem;
    height: 2.5rem;
}

.links {
    display: flex;
    flex-wrap: wrap;
    flex-direction:row;
    align-items: center;
    justify-content: center;
}

.link {
    object-fit: cover;
    height: 3rem;
    margin: 2rem 1rem 2rem 1rem;
}

.link:hover {
    opacity: 60%;
    cursor: pointer;

}
