#intro {
    height: calc(100vh - 4rem);
    width: 100vw;
    max-width: 80vw;
    overflow: hidden;
    margin: 0 auto;
    position: relative;
}

.introContent {
    height: 100vh;
    width: 100vw;
    font-size: 4rem;
    font-weight: 600;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.hi {
    font-size: 1.5rem;
    font-weight: 100;
}
.introName {
    color:#2C7865;
}
.introRole {
    font-size: 1.5rem;
}
.introPara {
    padding-top: 1rem;
    font-size: medium;
    font-weight: 400;
    color:#2C7865;
}


@media (width <= 375px) {
    .introContent {
        font-size:10vw;
    }
    .hi {
        font-size:6vw;
    }
    .introHeader, .introName , .introRole {
        font-size:8vw;
    }
    .introPara {
        font-size:4vw;
    }
}