.navbar {
    height: 4rem;
    /* background: rgba(255,170,1,0.2); */
    background: rgba(255,255,255,0.2);
    border-radius: 1rem;
    width: 100vw;
    margin: auto;
    max-width: 80vw;
    padding: 1rem 2rem;
    display: flex;
    align-items: center;    
    justify-content: space-between;
    position: sticky;
    top: 0;
    z-index: 10;
}

.logo {
    object-fit: cover;
    height: 3rem;
    width: 4rem;
}

.desktopMenuListItem {
    margin: 1rem;
    cursor: pointer;
    font-weight: 600;
}
.desktopMenuListItem:hover {
    color: #2C7865;
    padding-bottom: 0.25rem;
    border-bottom: 2px solid #2C7865;
}

.desktopMenuBtn {
    background: white;
    color: black;
    border: none;
    display: flex;
    align-items:center;
    justify-content: center;
    padding: 0 1rem;
    height: 2rem;
    width: auto;
    border-radius: 1rem;
    background: #2C7865;
    color:white;
}
.desktopMenuBtn:hover {
    opacity: 85%;
    cursor: pointer;
}

.desktopMenuImg {
    object-fit: cover;
    height:1rem;
    width: 1rem;
    padding: 0px 5px;
    
}

.active {
    color:#2C7865;
    padding-bottom: 0.1rem;
    border-bottom: 2px solid #2C7865;
}


.navMenu {
    position: absolute;
    top: 4rem;
    right: 2rem;
    z-index: 2;
    display: flex;
    flex-direction: column;
    height:fit-content;
    width: 8.5rem;
    background: rgba(40,40,40,0.5);
    border-radius: 1rem;
    padding:0.5rem ;
    margin-top: 1rem ;
}

.mobileMenu {
    display: none;
    object-fit: cover;
    height: 2rem;
}

.mobileMenuListItem {
    color: white;
    margin: 0.2rem;
    padding: 0.25rem 1rem;
}

@media (width <= 530px) {
    .mobileMenu {
        display: flex;
    }
    .desktopMenu {
        display: none;
    }
    .desktopMenuBtn {
        display: none;
    }
    .active {
        border-bottom:none;
    }
}
