#page {
    margin: 0 auto;
    min-height: calc(100vh - 4rem);
    width: 100vw;
    max-width: 60rem;
    display: flex;
}

.gallery {
    display: flex;
    margin: 0 8vw;
    flex-wrap: wrap;
}

.column {
    flex:50%;
    
}
.columnBG {
    flex:50%;
    background: rgba(40, 40, 40, 0.5);
    border-radius: 1rem;
}

.header {
    font-size: 2rem ;
    max-width: 100vw;
    padding: 3rem;
}

.paragraph {
    display: block;
    height: fit-content;
    min-height: 4rem;
    overflow-wrap: break-word;
    padding: 2rem 2rem 2rem 3rem;
    font-weight: 300;
    font-size: 1.1rem;
}

.madeWith {
    display: block;
    height: fit-content;
    min-height: 4rem;
    overflow-wrap: break-word;
    padding: 2rem 2rem 2rem 3rem;
    font-weight: 800;
    font-size: 1.8rem;
    color:rgba(57, 248, 232,0.8)
}


@media (width <= 1540px) {
    .column {
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
    }
  }