#works {
    margin: 0 auto;
    min-height: calc(100vh - 4rem);
    width: 100vw;
    max-width: 60rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.worksTitle {
    font-size: 3rem;
}

.workGallery {
    display:flex;
    align-items:center;
    justify-content: center;
    width: 100vw;
    max-width: 60rem;
    flex-wrap: wrap;
}

.workItem {
    object-fit:cover;
    border-radius: 1rem;
    height: 20rem;
    width: 16rem;
    margin: 3rem 0.5rem 0 0;
    align-items:center;
    justify-content: center;
}

.workGroup:hover {
    opacity: 60%;
    cursor: pointer;
}

.workDescBox {
    background: rgba(40, 40, 40, 0.5);
    margin: 0 0 0 0;
    border-radius: 1rem;
    display:flex;
    align-items:center;
    justify-content: center;
    width: 16rem;
    height: fit-content;
    min-height: 2rem;
}

.workMoreBtn {
    display: block;
    width: 8rem;
    height: 2rem;
    margin: 7rem 0;
    border: none;
    border-radius: 1rem;
    background: white;
    font-size: 1rem;
}

@media (width <= 545px) {
    .workItem {
        height: 45vw;
    }
    .workDescBox {
        min-height: 2rem;
    }
}